$('.siteList > .last').on({
    click: () => {
        $('.popWrapper').css('display', 'block')
        $('div.popTitle')[0].innerText = 'æ·»åŠ å¿«æ·æ ‡ç­¾'
        $('button.labelFinish')[0].innerText = 'æ·»åŠ '
    },
    touchstart: () => {
        $('.popWrapper').css('display', 'block')
        $('div.popTitle')[0].innerText = 'æ·»åŠ å¿«æ·æ ‡ç­¾'
        $('button.labelFinish')[0].innerText = 'æ·»åŠ '
    }
})
$('.labelFinish').on('click', () => {
    if($('button.labelFinish')[0].innerText==='æ·»åŠ '){
        let url = $('#popUrlInput').val()
        let name = $('#popNameInput').val()
        if(url){
            if (url.indexOf('http') !== 0) {
                url = 'https://' + url
            }
            if(!name){
                hashMap.push({
                    name: simplifyUrl(url)[0].toUpperCase(),
                    url: url
                })
                console.log(name)
            }else{
                hashMap.push({
                    name: name,
                    url: url
                })
            }
            render()
        }
    }
})

$('.labelCancel').on('click', () => {
    $('.popWrapper').css('display', 'none')
})

window.onbeforeunload = () => {
    const string = JSON.stringify(hashMap)
    localStorage.setItem('x', string)
}

